.woocommerce-form__label-for-checkbox {
  cursor: pointer;
  position: relative
}
.woocommerce-form__label-for-checkbox input {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden
  }
.woocommerce-form__label-for-checkbox input:checked ~ span:before {
        border-color: #6695a4;
        border-color: var(--color-blue);
      }
.woocommerce-form__label-for-checkbox input:checked ~ span:after {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
      }
.woocommerce-form__label-for-checkbox span:not(.required) {
    display: block;
    padding-left: 3.5rem;
    position: relative;
    line-height: 2.4rem;
    margin-bottom: 0;
    overflow: hidden
  }
.woocommerce-form__label-for-checkbox span:not(.required):before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 2.4rem;
      height: 2.4rem;
      border: 1px solid #D5D5D5;
      border: 1px solid var(--color-grey-30);
      border-radius: 4px;
    }
.woocommerce-form__label-for-checkbox span:not(.required):after {
      content: "";
      position: absolute;
      top: 5px;
      left: 5px;
      width: 1.4rem;
      height: 1.4rem;
      background-color: #2c2d85;
      background-color: #6695a4;
      background-color: var(--color-blue);
      border-radius: 50%;
      z-index: 1;
      opacity: 0;
      -webkit-transform: translate3d(-5px, 0, 0);
              transform: translate3d(-5px, 0, 0);
      pointer-events: none;
      transition: all .3s ease;
    }
