/* --------------------------------------------------------------
  Image ratio's
-------------------------------------------------------------- */
.i-ratio {
  position: relative;
  overflow: hidden
}
.i-ratio img {
    vertical-align: middle;
  }
.i-ratio:after {
    content: '';
    display: block;
    height: 0;
  }
.i-ratio.i-ratio--33:after {
      padding-bottom: 33.33%;
    }
.i-ratio.i-ratio--43:after {
      padding-bottom: 43.75%;
    }
.i-ratio.i-ratio--50:after {
      padding-bottom: 50%;
    }
.i-ratio.i-ratio--56:after {
      padding-bottom: 56.25%;
    }
.i-ratio.i-ratio--67:after {
      padding-bottom: 67.25%;
    }
.i-ratio.i-ratio--75:after {
      padding-bottom: 75%;
    }
.i-ratio.i-ratio--100:after {
      padding-bottom: 100%;
    }
.i-ratio.i-ratio--112:after {
      padding-bottom: 116.50%;
    }
.i-ratio.i-ratio--133:after {
      padding-bottom: 133.33%;
    }
